.NavContainer {
  width: 300px;
  display: flex;
  justify-content: space-between;
  color: #eae6ca;
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 1px;
  line-height: 35px;
}

.link{
  color: #eae6ca;
  text-decoration: none;
  margin: 0 8px;
}

.link:visited{
  color: #eae6ca;
  text-decoration: none;
}

.link:hover{
  color: #999950;;
}