.footerContainer {

}

.container {
  background-color: #424632;
  padding-top: 20px;
  width: 100%;
  min-width: 300px;
  flex-direction: column;
  align-content: center;
  justify-content: space-between;
  text-align: center;
  min-height: 150px;
}

.socialIcons {
  display: flex;
  width: 300px;
  justify-content: space-between;
}

.socialIcon {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container span{
  color:#eae6ca;
  font-family: 'Beer money';
  font-size: 17px;
}

.container h2{
  margin: 0;
}

.socialIcon img {
  width: 48px;
  height: 48px;
}